.pro {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.mrg-l {
  margin-left: 2%;
}
.aboutHeading {
  font-size: 4vmax !important;
  font-family: "Times New Roman" !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
}
.aboutContainer {
  display: flex;
  flex-direction: column;
}
.aboutBox {
  display: flex;
  flex-direction: row;
  margin: 2% 1%;
}
.imageBox {
  width: 50%;
}
.imageBox img {
  width: 100%;
}
.aboutText {
  width: 50%;
  font-size: 2.5vmax;
  font-family: "Times New Roman", Times, serif;
  margin: 0 5%;
  line-height: 1.7;
  text-shadow: 2px 2px 0 rgb(179 179 179);
}
.querryMapcontainer {
  display: flex;
  flex-direction: row;
}

.QuerryForm {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  height: 70%;
  width: 50%;
  padding: 2vmax;
  line-height: 2;
}
.QuerryForm > div {
  padding: 1vmax 0vmax;
  width: 60%;
}
.QuerryForm > div > input {
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.26);
  border-radius: 10px;
}

.QuerryForm > div > svg {
  position: absolute;
  transform: translateX(1vmax);
  padding-top: 1vmax;
}
.querry {
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  height: 10vmax;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.26);
  border: none;
  /* border-radius: 10px; */
}

.querryBtn {
  padding: 0.8vmax 0.5vmax;
  background-color: #e66329;
  color: #fff;
  /* border-radius: 10px; */
  border: none;
  width: 30%;
  font: 400 1.5vmax "Roboto";
  transition: all 0.5s;
}

.googleMap {
  width: 40%;
  margin-right: 2%;
  padding: 2vmax;
  margin-bottom: 7%;
}

@media only screen and (max-width: 900px) {
  .QuerryForm > div {
    width: 80%;
  }
  .querryBtn {
    width: 40%;
    font-size: 1.8vmax;
  }
}
@media only screen and (max-width: 600px) {
  .aboutBox {
    flex-direction: column;
  }
  .imageBox {
    width: 90%;
  }
  .aboutText {
    width: 90%;
  }

  .querryMapcontainer {
    flex-direction: column;
  }
  .QuerryForm {
    width: 90%;
    align-items: center;
  }
  .googleMap {
    width: 90%;
  }
}
