.LoginSignupContainer{
    width:100vw;
    max-width: 100%;
    height:100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    

}
.LoginSignupBox{
    width:35vw;
    height:75vh;
    background-color:rgb(235, 235, 235);
    box-shadow: 5px 5px 5px rgb(85, 81, 81);
}

.login_signup_toggle{
    display: flex;
    height:3vmax;
    justify-content: space-evenly;
    
}

.login_signup_toggle> p{
    color: #000;
    display:grid;
    place-items: center;
    cursor: pointer;
    font : 300 1.5vmax "Roboto";
    transition: all 0.5s;
    width: 100%;
}

.login_signup_toggle > p:hover{
    color:tomato;
}

.LoginSignupBox>div>button{
    background-color:tomato;
    border:none;
    height:3px;
    width:50%;
    transition: all 0.5s;
}

.loginForm,
.signupForm{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:space-evenly;
    height: 70%;
    padding:2vmax;
    
}
.signupForm{
    transform:translateY(-100%) translateX(-100vmax)
}
.loginForm>div>input,
.signupForm>div>input{
    padding:1vmax 4vmax;
    padding-right:1vmax;
    width:100%;
    box-sizing: border-box;
    border: 1px solid rgba(0,0,0,0.26);
    border: none;
    border-radius:10px;
}
.loginForm>div>svg,
.signupForm>div>svg{
    position:absolute;
    transform:translateX(1vmax);
    padding-top:1vmax ;
}

.loginForm>a{
    display: flex;
    font-size:1.6vmax;
    align-self: flex-end;
    text-decoration: none;
    color: rgb(50, 49, 49);
    transition: all 0.5s;
}

.loginForm>a:hover{
    color: rgb(0,0,0)
}
.loginBtn{
    padding:0.5vmax 1vmax;
    background-color:#e66329;
    width:70%;
    border: none;
    color: #fff;
    /* border-radius: 10px; */
    width: 40%;
    font:400 1.2vmax "Roboto";
    transition: all 0.5s;
}

.shiftToLeft{
    transform:translateX(-100vmax);
}
.shiftToNeutral{
    transform:translateX(0);
}
.shiftToNeutralForm{
    transform:translateX(0) translateY(-100%)
}
.shiftToRight{
    transform:translateX(100%)
}
 
.signUpBtn{
    padding:0.5vmax 1vmax;
    background-color:#e66329;
    border: none;
    /* border-radius: 10px; */
    width: 40%;
    font:400 1.2vmax "Roboto";
    transition: all 0.5s;
}

.profileContainer{
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    line-height:3;
}

.tomatoButton{
    background-color:tomato;
    width:80%;
    border: none;
    /* border-radius:8px; */
}
.greyButton{
    background-color:rgb(54, 52, 51);
    width:80%;
    border: none;
    /* border-radius:5px; */
    margin-top: 2vmax;
}
.profileButton{
    display: flex;
    justify-content: center;
    align-items: center;
    
    color:white;
    text-decoration: none;
}
.Button{
    display: flex;
    justify-content: center;
    align-items: center;

    color:white;
    text-decoration: none;
}


@media screen and (max-width: 600px) {
    .LoginSignUpContainer {
      background-color: white;
    }
    .LoginSignupBox {
      width: 90%;
      height: 95vh;
    }
    .login_signUp_toggle {
      height: 5vmax;
    }
    .login_signUp_toggle > p {
      font: 300 1.5vmax "Roboto";
    }
  
    .loginForm,
    .signUpForm {
      padding: 5vmax;
    }
  
    .loginForm > div > input,
    .signUpForm > div > input {
      padding: 2.5vmax 5vmax;
      font: 300 1.7vmax cursive;
    }
  
    .loginForm > div > svg,
    .signUpForm > div > svg {
      font-size: 2.8vmax;
    }
  
    .loginForm > a {
      font: 500 1.8vmax "Gill Sans";
    }
  
    #registerImage > img {
      width: 8vmax;
      border-radius: 100%;
    }
  
    #registerImage > input::file-selector-button {
      height: 7vh;
      font: 400 1.8vmax cursive;
    }
  
    .loginBtn,
    .signUpBtn {
      font: 300 1.9vmax "Roboto";
      padding: 1.8vmax;
    }
  }
  