.profileContainer{
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    line-height:3;
}

.tomatoButton{
    background-color:tomato;
    width:80%;
    border: none;
    /* border-radius:8px; */
}
.greyButton{
    background-color:rgb(54, 52, 51);
    width:80%;
    /* border-radius:5px; */
    border: none;
    margin-top: 2vmax;
}
.profileButton{
    display: flex;
    justify-content: center;
    align-items: center;
    
    color:white;
    text-decoration: none;
}
.Button{
    display: flex;
    justify-content: center;
    align-items: center;

    color:white;
    text-decoration: none;
}

.pageNotFound{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 25vw;
    flex-direction:column;
    line-height:2
}
.pageNotFound a {
    text-decoration: none;
    font-weight: bold;
    color: #fff;
    padding:10px;
    background-color: rgb(229, 79, 25);
    /* border-radius:10px */
    border: none;
}
.pageNotFound svg {
    font-size:6vmax !important;
    color: rgb(229, 79, 25);
}

