.container2{
    display: flex;
    margin: 2% 2% 0% 2%; 
    flex-direction: row;
}
.carosl{
    /* border:0 1px solid #ccc; */
    /* box-shadow:  5px 3px 3px rgb(124, 110, 110); */
    width: 40% !important;
}
.imageCarousel{
    width:50%
}
.contain{
 width:70%   
}

.carousel .slider-wrapper{
    object-fit: cover; 
    
}

.carousel .thumb{
    display:none !important;
}
.carousel .carousel-status {
    display:none !important;
}


.details{
    width: 100%;
    height: 100%;
    color: black
}
.mrp{
    text-decoration: line-through;
}

.detailView{
    width: 60%;
    line-height: 3vmax;
    margin-top: 2%;
    margin-left: 3%;
    
}

.bold{
    font-size:3vmax
}
.orange{
    color: #ff7744 !important;
}
.descText{
font-size:2vmax
}

.qtybtn{
    width:5%;
    background-color: blanchedalmond;
    color: #000;
}
.input{
    width:8%;
    display:flex;
    text-align:center;
}

.cartbtn{
    padding:1vmax;
    border:none;
    /* border-radius:10px; */
    background-color:#e66329;
    color: #fff;
    margin: 10px 0px 5px 0;
}
.submitBtn{
    padding:1vmax;
    border:none;
    /* border-radius: 10px; */
    background-color:#e66329;
    color: #fff;
}
.submitDialog{
    display: flex;
    flex-direction: column;
}
.reviewCard{
    margin:2%
}

@media only screen and (max-width: 600px) {
    .container2 {
        display: flex;
        flex-direction: column;
    }
    .carosl {
        width: 100% !important;
    }
    .detailView {
        width: 95%;
        line-height: 3vmax;
        margin-top: 2%;
        margin-left: 3%;
    }
    .reviewCard{
        margin:5%
    }
  }
