.shippingContainer {
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.shippingBox {
  width: 50%;
  height: 75vh;
  background-color: rgb(235, 235, 235);
  box-shadow: 5px 5px px rgb(85, 81, 81);
}
.shippingHeading {
  text-align: center;
  padding: 1.5vmax;
  line-height: 2v;
}

.shippingForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 70%;
  padding: 2vmax;
}
.shippingForm > div {
  display: flex;
  align-items: center;
  width: 80%;
}
.shippingForm > div > input,
.shippingForm > div > select {
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.26);
  border-radius: 10px;
}
.shippingAdress {
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  height: 10vmax;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.26);
  border: none;
  /* border-radius: 10px; */
}
.shippingAdressIcon {
  /* position: relative !important; */
}

.shippingForm > div > svg {
  position: absolute;
  transform: translateX(1vmax);
  /* padding-top: 1.4vmax; */
}

.shippingBtn {
  border: 2px solid #373737;
  padding: 1vmax;
  background-color: tomato;
  /* border-radius: 10px; */
  border: none;
  color: #fff;
  width: 80%;
  font: 400 1.2vmax "Roboto";
  transition: all 0.5s;
}

@media screen and (max-width: 600px) {
  .shippingBox {
    width: 90%;
    height: auto;
  }

  .shippingHeading {
    font: 400 6vw "Roboto";
    padding: 5vw;
  }

  .shippingForm {
    padding: 11vw;
  }

  .shippingForm > div > input,
  .shippingForm > div > select {
    padding: 5vw 10vw;
    font: 300 4vw cursive;
  }

  .shippingForm > div > svg {
    font-size: 6vw;
    transform: translateX(3vw);
  }

  .shippingBtn {
    font: 300 4vw "Roboto";
    padding: 4vw;
  }
}
