.loading{
    width: 100vw;
    height: 100vh;
    display: grid;
    place-items: center;
    max-width: 100%;
}

.loading > div{
    display: flex;
    align-items: center;
    width: 10vmax;
    /* background-color: tomato; */
    height: 10vmax;
    border-bottom: 2px solid #5a5353;
    border-radius: 80%;
    animation:loadingRotate  500ms linear infinite;
}

@keyframes loadingRotate {
    to{
        transform: rotateZ(-360deg);
    }
}