.cartPage {
  padding: 5vmax;
}
.CartItemCard {
  display: flex;
  flex-direction: row;
  align-content: space-between;
  justify-content: space-evenly;
  align-items: center;
  line-height:1.5;
}
.cartContainer {
  display: grid;
  grid-template-columns: 3fr 2fr 1fr;
}
.cartHeader {
  display: grid;
  grid-template-columns: 3fr 2fr 1fr;
  background-color: #d35915;
  box-sizing: border-box;
  color: #fff;
}
.textRight {
  text-align: right;
}
.textCenter {
    text-align: center;
  }
.textLeft {
  text-align: left;
}

.cartHeading {
  margin: 2%;
}
.cartItemContainer {
  display: grid;
  grid-template-columns: 3fr 1fr;
}
.cartImage {
  width: 80%;
}
.cartInput {
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.cartInput > p {
  width: 90%;
}
.cartInput input[type="number"] {
  width: 15%;
  text-align: center;
}
.cartInput button {
  width: 10%;
  color:#000;
}
.subtotalContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}
.totalAmountContainer{
  display: grid;
  grid-template-columns: 4fr 1fr;
  text-align: center;
  line-height: 1.6;
  margin-top: 5%;
}
.cartTotalBox{
  border-top: 1px solid #d35915
}
.checkoutBtn{
  padding:8px 30px;
  background-color:#d35915;
  color:#fff;
  /* border-radius:10px; */
  border: none;
  cursor: pointer;
}
.cartProductName{
  text-decoration: none;
  color:#000
}
.itemRemoveButton{
  padding:5px;
  background-color:#d35915;
  color:#fff;
  /* border-radius:10px; */
  border: none;
  cursor:pointer;
}
.emptyCart{
  margin-top: 5%;
  display: flex;
  text-align: center;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  line-height:2;
  width:100%;
  height:100%;
}

.emptyCart>svg{
  color: #d35915;
  font-size:15vmax !important;
}
.emptyCart>a{
  background-color:#5c5b59;
  text-decoration: none;
  color: #fff;
  padding:8px 10px;
  /* border-radius:5px; */
  border: none;
}


@media screen and (max-width: 600px) {
  .cartPage {
    padding: 0;
    min-height: 60vh;
  }

  .cartHeader {
    width: 100%;
    font: 300 1.7vmax "Roboto";
    grid-template-columns: 3fr 1fr 1fr;
  }

  .cartContainer {
    margin-top: 10%;
    width: 100%;
    grid-template-columns: 2.5fr 1.5fr 1fr;
  }

  .cartInput {
    display:block;
    width: 100%;
  }

  .cartInput > button {
    width: 25%;
  }

  .cartInput input[type="number"] {
    width: 35%;

  }

  .cartSubtotal {
    padding: 1.5vmax;
    height: 20vmax;
    font: 300 2vmax "Roboto";
  }

  .cartGrossProfit {
    display: grid;
    grid-template-columns: 0fr 2fr;
  }

  .cartGrossProfitBox {
    padding: 2vmax;
    font: 300 2vmax "Roboto";
  }

  .checkOutBtn > button {
    padding: 2vmax 4vmax;
    width: 100%;
    font: 300 2vmax "Roboto";
  }
}
