.container4 {
  display: flex;
  margin: 2% 2% 0 2%;
  flex-wrap: wrap;
  width: 82%;
}

.image {
  width: 19%;
  margin: 2% 1% 2% 0;
  box-shadow: -1px 1px 8px #c1c1c1;
}
.prodImage {
  width: 80%;
}
.wrap {
  display: flex;
  flex-wrap: wrap;
}
.detailsText {
  font-size: 1vmax;
}

.left {
  width: 50%;
  align-items: center;
  justify-content: flex-start;
  display: flex;
}
.right {
  padding-left: 10px;
  width: 25%;
  align-items: center;
  justify-content: flex-end;
  display: flex;
}

.paginationBox {
  display: flex;
  justify-content: center;
}

.pagination {
  display: flex;
  justify-content: center;
  padding: 0;
}

.page-item {
  list-style: none;
  padding: 1vmax 1.5vmax;
  transition: all 0.5s;
  cursor: pointer;
}

.page-item :hover {
  color: #f36a01;
}

.page-link {
  color: #000;
  text-decoration: none;
}

.bold {
  font-weight: bold;
}
.filterBox {
  width: 12%;
  margin-top: 5vmax;
  margin-left: 2%;
}

.categoryBox {
  padding: 0;
}

.categoryLink {
  padding-top: 6px;
  list-style: none;
  color: #000;
  line-height: 2.5vmax;
  transition: all 0.5s;
  cursor: pointer;
  font-weight: 300;
}

.categoryLink:hover {
  color: rgb(137, 131, 131);
  font-weight: 400;
}
.flex {
  display: flex;
}
.lh {
  display: flex;
  /* justify-content: flex-start; */
}

.rh {
  display: flex;
  /* justify-content: flex-end; */
  text-decoration: line-through;
}
.priceContr {
  width: 85%;
  justify-content: space-between;
}
.clrFilterBtn {
  padding: 5px;
  color: #fff;
  background-color: #ff7733;
  border: 0.1px solid black;
  border: none;
  /* border-radius: 10px; */
}
.orange {
  color: #ff7744 !important;
}

@media only screen and (max-width: 600px) {
  .container4 {
    margin: 2% 0% 0 2%;
    width: 76%;
  }

  .filterBox {
    width: 20%;
    margin-top: 2vmax;
  }
  .categoryLink {
    font-size: 1.5vmax;
  }
}
