.resetPasswordContainer{
    width:100vw;
    max-width: 100%;
    height:100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    

}
.resetPasswordBox{
    width:35vw;
    height:75vh;
    background-color:rgb(235, 235, 235);
    box-shadow: 5px 5px px rgb(85, 81, 81);
}
.resetPasswordHeading{
    text-align:center;
    padding: 1.5vmax;
    line-height:2v;
}

.resetPasswordForm{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:space-evenly;
    height: 70%;
    padding:2vmax;
    
}
.resetPasswordForm>div>input{
    padding:1vmax 4vmax;
    padding-right:1vmax;
    width:100%;
    box-sizing: border-box;
    border: 1px solid rgba(0,0,0,0.26);
    /* border-radius:10px; */
    border: none;
}
.resetPasswordForm>div>svg{
    position:absolute;
    transform:translateX(1vmax);
    padding-top:1vmax ;
}



.tomatoButton{
    background-color:tomato;
    width:80%;
    /* border-radius:8px; */
    border: none;
}
.resetPasswordBtn{
    padding:0.5vmax 1vmax;
    background-color:tomato;
    /* border-radius: 10px; */
    border: none;
    color: #fff;
    width: 75%;
    font:400 1.2vmax "Roboto";
    transition: all 0.5s;
}



@media screen and (max-width: 600px) {
    .resetPasswordContainer {
      background-color: white;
    }
    .resetPasswordBox {
      width: 90%;
      height: 95vh;
    }
  
    .resetPasswordForm {
      padding: 5vmax;
    }
  
    .resetPasswordForm > div > input {
      padding: 2.5vmax 5vmax;
      font: 300 1.7vmax cursive;
    }
  
    .resetPasswordForm > div > svg {
      font-size: 2.8vmax;
    }
  
    .resetPasswordBtn {
      font: 300 1.9vmax "Roboto";
      padding: 1.8vmax;
    }
  }
  