
.container3{
    margin-left: 2%;
    margin-bottom: 4%;
}
.tr-container{
    width: 24%;
}
.productCard{
    margin-left: 2%;
    margin-bottom: 4%;
    width: 23%;
    transition: all 0.5s
}
.categories{
    width: 14%;
    overflow: hidden;
    margin: 0 0 1% 0;
    flex-wrap: wrap;
    transition: all 0.5s;
    text-decoration: none;
}
.catImage {
    width: 80%;
}
h1 {
       font-family: sans-serif;
       font-size:2.5vmax;
}
.productCard~p{
    width: 80%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    text-decoration: none;
}
.categoryText{
    display: flex;
    text-align: center;
    justify-content: center;
    font-size: 1.25vmax;
    color: black;

}
.spaced{
    margin-left:5%;  
}
.flex{
    display: flex;
    flex-wrap:wrap;
}
.smallText{
    font-size:0.6vmax;
}
.wrapped{
    display: flex;
    flex-wrap: wrap;
}

.detailText{
    font-size:1.5vmax;
    text-decoration: none;
    color: black;
    overflow:hidden;
    text-overflow:ellipsis;
    
}
.boldText{
    font-weight: 500;
}

.productCard:hover{
    box-shadow: 0 0 10px rgba(15,15,15,0.25);
    transform:translateX(-0.5vmax)
}


.categories:hover{
    box-shadow: 0 0 10px rgba(15,15,15,0.25);
    transform:translateX(-1%)
}
.descrText{
    font-size: 1.5vmax;
}

.orange{
    color: #ff7744 !important;
}

@media only screen and (max-width: 900px) {
    .categories {
        width: 20%;
    }
    .productCard{
        width: 31%;
    }
  }

  @media only screen and (max-width: 600px) {
    .categories {
        width: 23%;
    }
    .categoryText{
        font-size: 1.1vmax;
    }
    .productCard{
        width: 45%;
    }
 
  }