@import url('https://fonts.googleapis.com/css2?family=Orbitron&family=Corben&family=Righteous&display=swap') ;

*{
    padding: 0;
    margin: 0;
}
.HeaderLarge{
    display: flex;
    flex-direction: row;
    overflow: hidden;
    justify-content: space-between;
    height:4vmax;
    background:linear-gradient(to top, #fff 35%, #6a6a6a 100%);
    /* background-color: #ff6600; */
}

.Logo {
    display: flex;
    font-family: 'Corben';
    font-style: italic;
    align-items: center;
    width: 70%;
    object-fit: contain;
    text-decoration: none;
    color: #fff;
}

.LogoText {
    font-size:3vmax;
    display: flex;
    align-items: center;
    padding: 1%;
    margin-left: 2%;
    color: rgb(0, 0, 0);
    text-shadow: 2px 2px #6c6c6c;
}

.contact{
    display: flex;
    align-items: center;
    justify-content: center;
    width:20%;
    padding-top: 0.5%;
}
.icons{
    padding: 5px;
    font-size:2vmax;
    color: #000;
    text-decoration: none;
}


.search{
    display: flex;
    align-items: center;
    width: 100%;
}
.searchbox{
    width: 80%;
    padding-left:5px;
    height:70%;
    border-radius: 5px;
    border: 0.5px solid #b7b7b7;
}
.searchIcon{
    font-size: 3.5vmax;
    color:#000;
    height: 2vmax;
    position: absolute;
    margin-left: 39%;
}
.nav1{
    display: visible;
}
.nav2{
    display: none;
}
.profileBox{
    position: fixed;
    right: 1vmax;
    /* top: 0.8vmax !important; */
}
.profilepic{
    border-radius: 50%;
    width: 100%;
}
.MuiFab-root{
    width: 3vmax !important;
    height: 3vmax !important;
    min-height:0 !important;
}

.MuiSvgIcon-root{
    font-size: 2vmax !important;
}

.MuiSpeedDial-directionDown {
    flex-direction: column;
    position: absolute;
    top: 0.4vmax;
}

@media only screen and (max-width: 900px) {
    .LogoText {
        text-shadow: 1.3px 1.3px #6c6c6c;
    }
}

@media only screen and (max-width: 600px) {
.HeaderLarge{
    height:5vmax;
}
.Logo {
    width: 40%;
}
.LogoText {
    font-size:2.5vmax;
}
.large{
    font-size:3.5vmax
}
.search{
    width: 60%;
}
.searchIcon{
    margin-left: 35%;

}
.LogoText {
    text-shadow: 1px 1px #6c6c6c;
}
}
